import defaultStorage from './uniStorage'

// 底部tabbar列表
let tabbarListKey = 'globalTabbarList'
export function setTabbarList(data) {
  return defaultStorage.set(tabbarListKey, data)
}
export function getTabbarList() {
  return defaultStorage.get(tabbarListKey)
}
export function clearTabbarList() {
  return defaultStorage.remove(tabbarListKey)
}

// 主题色
let themeColorKey = 'globalThemeColor'
export function setThemeColor(data) {
  return defaultStorage.set(themeColorKey, data)
}
export function getThemeColor() {
  return defaultStorage.get(themeColorKey)
}
export function clearThemeColor() {
  return defaultStorage.remove(themeColorKey)
}
